.topbar {
    display: grid;
    width: 100vw;
    height: 10vw;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 2px;
    grid-template-areas: ". . . . . . . . . . . .";
}

.topbar__left {
    background-color: rgb(61, 240, 54);
}

.topbar__right {
    background-color: rgba(24, 24, 131, 0.9);
}

.topbar button {
    font-size: larger;
    color: white;
    border-radius: 12px;
}

.table__order {
    background-color: hotpink;
}
.table__order__list {
    background-color: silver;
}
.table__order__item {
    background-color: teal;
}

.new__order__map {
    background-color: rgb(121, 73, 121);
    color: rgb(103, 103, 143);
}
